import { TFunction } from 'i18next';
import { useMemo } from 'react';

type Mode = {
  labels: {
    title: string;
    submit: string;
  };
  isEdit: boolean;
};

export const useProductFormMode = (t: TFunction, productId: string) => {
  const isEditMode = !!productId;

  const mode: Mode = useMemo(() => {
    if (isEditMode) {
      return {
        labels: {
          title: t('product.edit.title', '  '),
          submit: t('product.edit.submit', ''),
        },
        isEdit: true,
      };
    } else {
      return {
        labels: {
          title: t('product.create.title', '  '),
          submit: t('product.create.submit', ''),
        },
        isEdit: false,
      };
    }
  }, [t, isEditMode]);

  return { mode };
};
